<template>
    <div class="box">
        <div class="title">简历解析</div>
        <div class="upload">
            <el-upload class="el-upload" drag :on-change="fileChange" multiple limit="3 " :on-success="handleSuccess"
                :on-error="handleError" ref="fileInput" :auto-upload="false" 
                :headers="{ 'Content-Type': '' }">
                <!-- <i class="el-icon-upload"></i> -->
                <div class="el-upload__text" style="transform: translateY(80px) scale(0.9);">将文件拖到此处，或<em>点击上传</em>
                </div>
                <div class="el-upload__tip" slot="tip">（支持格式：pdf|doc|docx|txt|odt|RTF|HTML|JPG等30多种格式, 最大8M,最多支持3个批量上传）
                </div>
            </el-upload>
            <el-button type="primary" class="button" @click="goPersonal">开始解析</el-button>
        </div>

    </div>
</template>

<script>
import axios from 'axios';
import { reqSaveResume } from '@/api/index'
export default {
    name: "Analyze",
    methods: {
        async goPersonal() {
            try {
                const response = await reqSaveResume(this.id, this.selectedFile.raw);
                this.$router.push('/userInfo/upload')
            } catch (error) {
                console.error(error);
            }
        },
        handleSuccess(response, file, fileList) {
            console.log(response);
        },
        handleError(error, file, fileList) {
            console.log(error, file)
        },
        async fileChange(file) {
            this.selectedFile = file
        }
    },
    computed: {
        id() {
            return this.$store.state.user.id
        },
    },
    data() {
        return {
            selectedFile: null
        }
    }
}

</script>

<style lang="scss" scoped>
.box {
    height: 700px;
    width: 100%;
    overflow: hidden;
    .title {
        width: 200px;
        height: 20px;
        font-weight: 700;
        font-size: 30px;
        margin: 0 auto;
        margin-top: 40px;
        margin-bottom: 100px;
        text-align: center;
        line-height: 120px;
    }

    .upload {
        width: 100%;
        height: 500px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-content: center;

        .el-upload {
            transform: scale(1.5, 1.6);
        }

        .button {
            width: 120px;
            height: 40px;
            margin: 0 auto;
        }   
    }

}
</style>
